@use "./var.scss";
@import "./var.scss";

.footerStyle {
    @include var.sizes($width:100%, $height:auto);
    @include var.box($margin:0, $padding:0);
    @include var.flexbox($display:flex,$align-items:center,$justify-content:center, $flex-direction:row);
    border-top: 1px solid var.$white;
    background-color: var.$dark;
    color: var.$white;
    text-align: center;


    .footerLink {
        @include var.sizes($width:10vw, $height:auto);

        svg {
            @include var.flexbox($display:flex,$align-items:center,$justify-content:center, $flex-direction:row);
            flex-direction: row;
            font-size: 30px;
            @include var.box($margin:10px, $padding:0);
            color: var.$brownishYellow;
        }
    }

    .footerHr {
        transform: rotate(90deg);
        @include var.sizes($width:100px, $height:auto);
        border: none;
        border-bottom: 1px solid var.$white;
    }

    .elerhetoseg {
        h1 {
            font-size: 1rem;
        }

        h3 {
            font-size: 1rem;
        }
    }

    .text {
        padding-right: 20px;

        h1 {
            font-size: 1rem;
        }

        p {
            font-size: 1rem;
        }
    }
}



@media(max-width:992px) {
    .footerStyle {
        display: flex;
        flex-direction: column;

        .footerLink {
            @include var.box($margin:0 auto, $padding:auto);
            display: flex;
            flex-direction: row;
            @include var.sizes($width:auto, $height:auto);

        }

        .footerHr {
            display: none;
        }

        .elerhetoseg {
            h1 {
                font-size: 1rem;
            }

            h3 {
                font-size: 1rem;
            }
        }

        .text {
            padding-right: 20px;

            h1 {
                font-size: 1rem;
            }

            p {
                font-size: 1rem;
            }
        }

        .text {
            text-align: center;
            @include var.box($margin:10px, $padding:auto);

        }
    }








}